

























































import Prompt from '@/components/zz-prompt/index.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
  components: {
	Prompt,uniPopup
  },
  data() {
	return {
	  // 控制弹框输入框显示, 在需要的地方调用this.promptVisible = true
	  promptVisible: true,
	  Signboard:'',
	  Signguid:'',
	  userInfo: '',
	  middle: false,
	  popupmodel: '',
	  popupInputName:'',
	  number:'', //编号,即data-id
	}
  },
  methods: {
	companynameClick(evt){
		uni.showToast({
			title: '禁止修改！',
			icon:"none"
		})
	},
	telephoneClick(evt){
		uni.showToast({
			title:'请前往安全中心修改',
			icon:'none'
		})
	},
	userClick(e){
		this.popupmodel = e.currentTarget.dataset.com;
		this.number=e.currentTarget.dataset.id;
		this.middle = true;
	},
	popupInput:function(evt){
		this.popupInputName=evt.target.value;
	},
	hidePopup(evt){
		switch (this.number){
			case '3':
				this.userInfo.phone=this.popupInputName;
				break;
			case '4':
				this.userInfo.rise=this.popupInputName;
				break;
			case '5':
				this.userInfo.taxcode=this.popupInputName;
				break;
			case '6':
				this.userInfo.bank=this.popupInputName;
				break;
			case '7':
				this.userInfo.address=this.popupInputName;
				break;
			default:
				break;
		}
		this.middle=false;
	},
	cancelPopup(evt){
		this.middle=false;
	},
	changePersonal(evt){
		uni.request({
			url:'http://192.168.199.194/mobile/personal/updUserInfo',
			method:'GET',
			data:{
				Ident_Signboard: this.Signboard,
				Ident_Signguid: this.Signguid,
				cm_tax_code:this.userInfo.taxcode, //税号
				cm_bank_code:this.userInfo.bank, //开户行
				cm_address:this.userInfo.address, //地址
				rise:this.userInfo.rise, // 抬头
				phone:this.userInfo.phone //座机
			},
			success: (res) => {
				if(res.data.status==0){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}else{
					uni.showToast({
						title:'保存失败',
						icon:'none'
					})
				}
			},
		})
	}
  },
 onLoad:function(){
	  var jsonList=uni.getStorageSync("jsonList");
	  var data = JSON.parse(jsonList); //JSON字符串转对象
	  this.Signboard = data.Ident_Signboard;
	  this.Signguid = data.Ident_Signguid;
	  
  	uni.request({
  		url: 'http://192.168.199.194/mobile/personal/getPersonInfo',
		method: 'GET',
		data:{
			Ident_Signboard: this.Signboard,
			Ident_Signguid: this.Signguid
		},
		success: (res) => {
			console.log(res)
			if(res.data.status == 0){ 
				this.userInfo = res.data.userInfo
			}
		}
  	})
  }
}
